.infoBox {
  flex: 1;
}

.infobox-container {
  background-color: white;
  /* background-color: rgb(235, 231, 231); */
}

.infoBox:not(:last-child) {
  margin-right: 10px;
}

.infoBox--selected {
  border-top: 10px solid #5971db;
}

.infoBox--red {
  border-color: red;
}

.infoBox__cases--blue {
  color: #5971db !important;
}

.infoBox__cases {
  color: #cc1034;
  font-weight: 600;
  font-size: 1.75rem;
  margin-bottom: 0.5rem;
}

.infoBox__total {
  color: #6c757d;
  /* !important : override materialui */
  font-weight: 700 !important;
  font-size: 0.8rem !important;
  margin-top: 15px !important;
}